import { DARK_MODE, LIGHT_MODE } from '../../actions/actionTheme'

const initialState = { mode: 'dark' }

function themeReducer(state = initialState, action) {
  switch (action.type) {
    case DARK_MODE:
      return { ...state, mode: action.payload }
    case LIGHT_MODE:
      return { ...state, mode: action.payload }
    default:
      return state
  }
}

export default themeReducer
